<template>
  <div class="container preferences-view">

    <ul class="tab tab-block">
      <li v-for="(tab, tabId) in tabs" class="tab-item" :key="tabId">
        <a class="c-hand"
          :class="{active: tabId === currentTab, badge: tab.badge}"
          @click="currentTab = tabId"
          :data-badge="tab.badge"
        >{{tab.name}}</a>
      </li>
    </ul>

    <!-- Basic -->
    <div class="card" v-show="currentTab === 'general'">
      <div class="card-header">
        <button class="btn btn-primary float-right">Save</button>
        <div class="card-title h5">Basic Settings</div>
      </div>
      <div class="card-body">
        <form class="form-horizontal">
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label">Omega Name</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="text">
            </div>
          </div>
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label">Timezone</label>
            </div>
            <div class="col-8 col-sm-12">
              <select class="form-select">
                <option>Choose an option</option>
                <option>Slack</option>
                <option>Skype</option>
                <option>Hipchat</option>
              </select>
            </div>
          </div>
        </form>
      </div>
    </div>

    <!-- Security -->
    <div class="card" v-show="currentTab === 'general'">
      <div class="card-header">
        <button class="btn btn-primary float-right">Save</button>
        <div class="card-title h5">Security Settings</div>
      </div>
      <div class="card-body">
        <form class="form-horizontal">
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label">Current Password</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="password">
            </div>
          </div>
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label">New Password</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="password">
            </div>
          </div>
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label" >Verify Password</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="password">
            </div>
          </div>
        </form>
      </div>
    </div>

    <!-- Wi-Fi -->
    <div class="card" v-show="currentTab === 'wifi'">
      <div class="card-header">
        <button class="btn btn-primary float-right" style="{width: 100%}">Join Other Networks ...</button>
        <div class="card-title h5">Wi-Fi Settings</div>
      </div>
      <div class="card-body">
        <table class="table table-hover">
          <tbody>
            <tr :class="{active: false}">
              <td>OnionWiFi</td>
              <td class="wifi-icons">
                <i class="mdi mdi-lock tooltip" data-tooltip="Secure Network"></i>
                <i class="mdi mdi-wifi"></i>
              </td>
            </tr>
            <tr :class="{active: false}">
              <td>OnionWiFi</td>
              <td class="wifi-icons">
                <i class="mdi mdi-lock tooltip" data-tooltip="Secure Network"></i>
                <i class="mdi mdi-wifi"></i>
              </td>
            </tr>
            <tr :class="{active: false}">
              <td>OnionWiFi</td>
              <td class="wifi-icons">
                <i class="mdi mdi-lock tooltip" data-tooltip="Secure Network"></i>
                <i class="mdi mdi-wifi"></i>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- AP -->
    <div class="card" v-show="currentTab === 'wifi'">
      <div class="card-header">
        <button class="btn btn-primary float-right">Save</button>
        <div class="card-title h5">Access Point Setup</div>
      </div>
      <div class="card-body">
        <form class="form-horizontal">
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label">AP Network (SSID)</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="text">
            </div>
          </div>
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label">Security</label>
            </div>
            <div class="col-8 col-sm-12">
              <select class="form-select">
                <option>None</option>
                <option>WEP</option>
                <option>WPA</option>
                <option>WPA2</option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label" >AP Password</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="password">
            </div>
          </div>
          <div class="form-group">
            <div class="col-4 col-sm-12">
              <label class="form-label" >IP Address</label>
            </div>
            <div class="col-8 col-sm-12">
              <input class="form-input" type="password">
            </div>
          </div>
        </form>
      </div>
    </div>

    <!-- System Upgrade -->
    <div class="card" v-show="currentTab === 'upgrade'">
      <div class="card-header">
        <button class="btn btn-primary loading float-right">Upgrade</button>
        <div class="card-title h5">System Upgrade</div>
      </div>
      <div class="card-body">
        <div class="toast toast-warning">
          <h4>Attention!</h4>
          Firmware upgrade will erase all files outside of the <code>/root</code> and <code>/etc</code> directories. Please backup any custom files before proceeding.
        </div>
        <table class="table">
          <tbody>
            <tr>
              <td>Model</td>
              <td>Onion Omega2+</td>
            </tr>
            <tr>
              <td>Firmware</td>
              <td>0.2.0 (b182)</td>
            </tr>
            <tr>
              <td>Latest Firmware</td>
              <td>0.1.10 (b160)</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- Restore -->
    <div class="card" v-show="currentTab === 'reset'">
      <div class="card-header">
        <button class="btn btn-error float-right">Reset</button>
        <div class="card-title h5">Factory Reset</div>
      </div>
      <div class="card-body">
        <div class="toast toast-error reset-warning">
          <h4>Attention!</h4>
          This will erase everything on the Omega and reset the device to factory default settings. Please backup any custom files on the device before proceeding.
          <br>
          <br>
          <h6>Once the reset is done, your Omega's LED will stop flashing.</h6>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'PreferencesApp',
  data () {
    return {
      currentTab: 'general',
      tabs: {
        general: {name: 'General'},
        wifi: {name: 'Wi-Fi'},
        upgrade: {name: 'Upgrade'},
        reset: {name: 'Reset'}
      }
    }
  },
  computed: {
  },
  props: {
  },
  methods: {
  },
  created: function () {
  }
}
</script>

<style scoped>
.menu {
}

.card {
  text-align: left;
  max-width: 480px;
  margin: auto;
  margin-top: 20px;
}
.preferences-view {
  padding: 20px;
  background-color: #f8f9fa;
  height: 100%;
  overflow: auto;
}

.wifi-icons {
  text-align: right;
}
</style>



